

.precios {
    width: 100%;
    height: 780px;
    text-align: center;
    background-color: #f4f7fc;
    
  }

    .precios_head{
      display: flex;
      align-items: center;
      justify-content: center;
      
    }
      .precios_head_title{
        margin-top: 80px;
      }
  
      .precios_head h4{
        color: var(--color-rojo-logo);
        font-weight: 600;
        text-transform: uppercase;
      }
  
      .precios_head h2{
        display: inline-block;
        font-weight: 600;
        line-height: 1.4;
      }
    
    .precios_options{
      height: 100px;
      display: flex;
      align-items: center;
      justify-content: center;

    }
  
      .precios_buttons{
        width: auto;
        box-shadow: 1px 4px 20px -2px rgb(0 0 0 / 10%);
        padding: 4px;
        background-color: #fff;
        border-radius: 32px;
      }
  
        .precios_buttons button {
          border-radius: 32px;
          border: none;
          position: relative;
          padding: 8px 16px;
          cursor: pointer;
          background-color: transparent;
          color: #777;
  
        }
  
        .precios_buttons button.activetab, .precios_buttons button.activetab:hover {
          background-color: var(--color-rojo-logo);
          color: #fff;
        }
  
        .precios_buttons button:hover {
          background-color: rgba(119, 119, 119, 0.411);
        }
  
          .precios_buttons button label {
              display: inline;
              background-color: #43a047;
              color: #fff;
              font-size: 10px;
              padding: 0 4px;
              border-radius: 8px;
              margin-left: 4px;
          }
  
    
    .precios_contents{
      margin-left: 140px;
      margin-right: 140px;
      display: flex;
      flex-wrap: wrap;
      gap: 23px;
    }
    
      .precios_notas{
        margin-top: 30px;
        color: var(--color-rojo-logo);
        font-size: 13px;
        font-weight: 550;
      }
  

  
  .contenttab, .modalcontent{
   display: none;
  }
  
  .active-contenttab, .activemodal{
   display: block;
  }


/* Seccion Precios responsive */
@media screen and (max-width: 1200px ){
  .precios {
    height: 1230px;
  }

    .precios_contents{
      margin-left: 0px;
      margin-right: 0px;
      width: 100%;
      justify-content: center;
      align-items: center;
    }

    

}

@media screen and (max-width: 600px ){
  .precios {
    height: 2200px;
  }
  .precios_head_title {
    margin-bottom: 15px;
  }

  .precios_contents{
    gap: 0px;
  }
  .precios_options{
    height: 150px;

  }
    .precios_buttons{
      width: 80%;
      margin-bottom: 15px;
    }
      .precios_buttons button {
        width: 100%;

      }


}
  